<template>
	<div class="app-content" v-if="Object.keys(productInfo).length">
		<div class="container">
			<div class="page-title">
				<h1>{{ productInfo.name.ua }}</h1>
				<div class="actions">
					<button class="btn btn-success" @click="savePage()">
						<span class="fas fa-save"></span>
						Сохранить
					</button>
				</div>
			</div>
			<div class="page-content">
				<div class="progress" v-if="saveInProgress">
					<div class="progress-bar progress-bar-striped progress-bar-animated" role="progressbar" style="width: 100%"></div>
				</div>
				<div class="alert alert-success" v-if="isSavedSuccess" role="alert">
					Изменения успешно сохранены
				</div>
				<div class="alert alert-danger" v-if="isFieldsEmpty" role="alert">
					Заполните все поля
				</div>
				<div class="product-section">
					<div class="row">
						<div class="col-3">
							<div class="image">
								<img class="shadow rounded" :src="productInfo.image">
							</div>
						</div>
						<div class="col-9">
							<div class="info">
								<fieldset>
									<legend>Общая информация</legend>
									<div class="row">
										<div class="col-4">
											<label class="form-label">Артикул</label>
											<div class="input-group">
												<input type="number" class="form-control" v-model="productInfo.code" disabled>
											</div>
										</div>
										<div class="col-4">
											<label class="form-label">Стоимость</label>
											<div class="input-group">
												<input type="number" class="form-control" v-model="productInfo.cost">
											</div>
										</div>
                                        <div class="col-4">
                                            <label class="form-label">Наличие</label>
                                            <select class="form-select" v-model="productInfo.isAvailable">
                                                <option :value="true">В наличии</option>
                                                <option :value="false">Нет в наличии</option>
                                            </select>
                                        </div>
										<div class="col-12">
											<label class="form-label">Наименование</label>
											<InputLangGroup v-model="productInfo.name" :content="productInfo.name"/>
										</div>
										<div class="col-12">
											<label class="form-label">Фото товара</label>
											<div class="input-group mb-3">
												<input type="text" class="form-control" placeholder="Ссылка на фото" v-model="productInfo.image">
											</div>
										</div>
									</div>
								</fieldset>
                                <fieldset>
									<legend>Категория</legend>
									<div class="row">
										<div class="col">
											<div class="input-group">
												<select class="form-select" v-model="productInfo.category" disabled>
                                                    <option v-for="(category, j) in categories" :key="j" :value="category.id">{{ category.name.ua }}</option>
												</select>
											</div>
										</div>
									</div>
								</fieldset>
								<fieldset v-if="currentCategory">
									<legend>Опции</legend>
									<div class="row">
										<div class="col">
											<ul class="list-group list-group-flush">
												<li v-for="(filter, i) in productInfo.options" :key="i" class="list-group-item">
													<strong>{{ getFilterName(filter).option }}: </strong>
                                                    <span>{{ getFilterName(filter).filter }}</span>
													<span class="badge bg-danger rounded" @click="delleteFilter(i)">
														<span class="fas fa-times"></span>
													</span>
												</li>
											</ul>
											<div class="input-group">
												<select class="form-select" v-model="selectedFilter">
													<optgroup :label="option.name.ua" v-for="(option, i) in currentCategory.list" :key="i">
														<option v-for="(filter, j) in option.list" :key="j" :value="filter.id">{{ filter.name.ua }}</option>
													</optgroup>
												</select>
												<button class="btn btn-primary" @click="addFilter()">Добавить</button>
											</div>
										</div>
									</div>
								</fieldset>
								<fieldset v-if="productInfo.characteristics.length">
									<legend>Характеристики</legend>
									<div class="row">
										<div class="col">
											<div class="input-group" v-for="(item, i) in productInfo.characteristics" :key="i">
												<span class="input-group-text">{{ item.name['ua'] }}</span>
												<span class="input-group-text">UA</span>
												<input type="text" v-model="item.value['ua']" class="form-control">
												<span class="input-group-text">RU</span>
												<input type="text" v-model="item.value['ru']" class="form-control">
												<span class="input-group-text">EN</span>
												<input type="text" v-model="item.value['en']" class="form-control">
                                                <button class="btn btn-danger" @click="delleteCharacteristic(i)">
													<span class="fas fa-trash"></span>
												</button>
											</div>
										</div>
									</div>
								</fieldset>
								<fieldset>
									<legend>Описание товара UA</legend>
									<div class="row">
										<div class="col">
											<Editor ref="productDescriptionUA" selector="productInfoEditorUA" buttons="bold, italic, underline, ul, ol, link, undo, redo"/>
										</div>
									</div>
								</fieldset>
                                <fieldset>
									<legend>Описание товара RU</legend>
									<div class="row">
										<div class="col">
											<Editor ref="productDescriptionRU" selector="productInfoEditorRU" buttons="bold, italic, underline, ul, ol, link, undo, redo"/>
										</div>
									</div>
								</fieldset>
                                <fieldset>
									<legend>Описание товара EN</legend>
									<div class="row">
										<div class="col">
											<Editor ref="productDescriptionEN" selector="productInfoEditorEN" buttons="bold, italic, underline, ul, ol, link, undo, redo"/>
										</div>
									</div>
								</fieldset>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import Editor from '../components/Editor.vue';
import InputLangGroup from '../components/InputLangGroup.vue';

export default {
    data() {
        return {
            productInfo: {},
            saveInProgress: false,
            isSavedSuccess: false,
            isNewProduct: false,
            isFieldsEmpty: false,
            qrCode: null,
            selectedFilter: ''
        }
    },
    components: {
        Editor,
        InputLangGroup
    },
    computed: {
        productId() {
            return this.$route.params.id;
        },
        product() {
            return this.$store.getters['getProducts'].filter((product) => {
                return (product.code == this.productId);
            })[0];
        },
        categories() {
            return this.$store.getters['getCategories'];
        },
        currentCategory() {
            if (!this.productInfo.category.length) {
                return false
            } else {
                return this.categories.filter((el) => {
                    return (el.id == this.productInfo.category);
                })[0];
            }
        }
    },
    watch: {
        product(value) {
            this.productInfo = value;
            setTimeout(() => {
                this.$refs.productDescriptionUA.setContent(this.productInfo.description['ua']);
                this.$refs.productDescriptionRU.setContent(this.productInfo.description['ru']);
                this.$refs.productDescriptionEN.setContent(this.productInfo.description['en']);
            }, 1000);
        }
    },
    methods: {
        getFilterName(id) {
            for (var i = 0; i < this.currentCategory.list.length; i++) {
                for (var j = 0; j < this.currentCategory.list[i].list.length; j++) {
                    if (this.currentCategory.list[i].list[j].id == id) 
                        return {
                            option: this.currentCategory.list[i].name['ua'],
                            filter: this.currentCategory.list[i].list[j].name['ua']
                        }
                            
                }	
            }
        },
        delleteFilter(index) {
            this.productInfo.options.splice(index, 1);
        },
        delleteCharacteristic(index) {
			this.productInfo.characteristics.splice(index, 1);
		},
        addFilter() {
            if (!!this.selectedFilter.length) {
                if (this.productInfo.options.indexOf(this.selectedFilter) == (-1)) {
                    this.productInfo.options.push(this.selectedFilter);
                    let elId = this.selectedFilter.slice(0, -2);
                    this.selectedFilter = '';
                    let alreadyExist = false;
                    for (let i = 0; i < this.productInfo.characteristics.length; i++) {
                        if (this.productInfo.characteristics[i].id == elId) {
                            alreadyExist = true;
                            return;
                        }
                    }
                    if (!alreadyExist) {
                        this.productInfo.characteristics.push({
                            id: this.$store.getters['getOptionById'](elId)['id'],
                            name: this.$store.getters['getOptionById'](elId)['name'],
                            value: {
                                ua: '',
                                ru: '',
                                en: ''
                            }
                        });
                    }
                }
            }
        },
        savePage() {
            this.productInfo.description.ua = this.$refs.productDescriptionUA.getContent();
            this.productInfo.description.ru = this.$refs.productDescriptionRU.getContent();
            this.productInfo.description.en = this.$refs.productDescriptionEN.getContent();
            if (
                !!this.productInfo.code &&
                !!this.productInfo.name &&
                !!this.productInfo.cost &&
                !!this.productInfo.image &&
                !!this.productInfo.description &&
                !!this.productInfo.options.length
            ) {
                this.saveInProgress = true;
                this.$store.dispatch('saveProductPage', this.productInfo)
                    .then(() => {
                        this.saveInProgress = false;
                        this.isSavedSuccess = true;
                        setTimeout(() => {
                            this.isSavedSuccess = false;
                        }, 3000);
                    });
            } else {
                this.isFieldsEmpty = true;
                setTimeout(() => {
                    this.isFieldsEmpty = false;
                }, 3000);
            }
            
        },
        // getQR: function () {
        // 	QRcode.toDataURL('https:' + this.productId, function (err, url) {
        // 		this.qrCode = url;
        // 	}.bind(this));
        // }
    },
    created: function() {
        
    },
    mounted: function () {
        // this.getQR();
        this.productInfo = this.product;
        if (!!this.productInfo.description) {
            setTimeout(() => {
                this.$refs.productDescriptionUA.setContent(this.productInfo.description['ua']);
                this.$refs.productDescriptionRU.setContent(this.productInfo.description['ru']);
                this.$refs.productDescriptionEN.setContent(this.productInfo.description['en']);
            }, 1000);
        }
    }
};
</script>